import React, { useState } from 'react';
import scrollTo from '../scrollTo';

const Navigation = () => {
  const [isOpened, setIsOpened] = useState(false);

  const clickHandler = e => {
    e.preventDefault();
    setIsOpened(!isOpened);
  };

  const clickNav = (e, targetSelector) => {
    e.preventDefault();
    scrollTo(targetSelector);
  };

  return (
    <div>
      <a
        href=''
        className={`menu-toggle rounded bg-dark text-white ${
          isOpened ? 'active' : ''
        }`}
        onClick={e => clickHandler(e)}
      >
        <i className={'fas ' + (isOpened ? 'fa-times' : 'fa-bars')}></i>
      </a>
      <nav
        id='sidebar-wrapper'
        className={`bg-dark ${isOpened ? 'active' : ''}`}
        onClick={e => clickHandler(e)}
      >
        <ul className='sidebar-nav'>
          {[
            {
              to: '#about',
              title: 'About'
            },
            {
              to: '#services',
              title: 'Services'
            },
            {
              to: '#portfolio',
              title: 'Shop'
            },
            {
              to: '#pricing',
              title: 'Pricing'
            },
            {
              to: '#contact',
              title: 'Contact'
            }
          ].map(({ to, title }, i) => (
            <li key={i} className='sidebar-nav-item'>
              <a
                href=''
                onClick={e => clickNav(e, to)}
                className='text-white font-weight-bold'
              >
                {title}
              </a>
            </li>
          ))}
        </ul>
      </nav>
    </div>
  );
};

export default Navigation;
