import React, { useEffect, useState } from 'react';
import scrollTo from '../scrollTo';

interface HeaderProps {}

const Header: React.FC<HeaderProps> = () => {
  const [height, setHeight] = useState('0px');

  const resizeHeight = () => {
    setHeight(`${window.document.documentElement.clientHeight}px`);
  };

  useEffect(() => {
    // [NOTE]: CSSの100vhではSafariのアドレスバーを含めて計算してしまうためJS制御で縦幅100%にする。
    resizeHeight();
    window.addEventListener('resize', resizeHeight);

    return () => {
      window.removeEventListener('resize', resizeHeight);
    };
  }, []);

  const onClick = (e, targetSelector: string) => {
    e.preventDefault();
    const targetElement: Element = document.querySelector(targetSelector);
    const targetTop: number = targetElement.getBoundingClientRect().top;
    scrollTo(window.scrollY + targetTop);
  };

  return (
    <header
      id='header'
      className='masthead d-flex'
      style={{
        height
      }}
    >
      <a className='header-logo' href='' onClick={e => onClick(e, '#about')} />
      <div className='overlay'></div>
    </header>
  );
};

export default Header;
